.opt-body {
  background-color: #fff;
  font-family: "GothaProReg", Arial, sans-serif;
  font-size: 14px;
  line-height: normal;
  color: #000000;
  padding: 0;
  margin: 0;
  /*Section-hero*/
}
.opt-body * {
  box-sizing: border-box;
}
.opt-body a {
