}
.opt-body ul,
.opt-body ol {
  list-style: none;
}
.opt-body .text-center-container {
  margin: 0 auto;
  max-width: 1150px;
  width: 100%;
}
@media (max-width: 992px) {
  .opt-body .section-send__form {
    margin-left: auto;
    margin-right: auto;
  }
}
.opt-body .content-container {
  width: 100%;
  max-width: 1170px;
  height: auto;
  margin: 0 auto;
}
@media (max-width: 1279px) {
  .opt-body .content-container {
    max-width: 950px;
  }
}
@media (max-width: 979px) {
  .opt-body .content-container {
    max-width: 750px;
  }
}
@media (max-width: 767px) {
  .opt-body .content-container {
    width: 90%;
