}
@media (max-width: 479px) {
  .opt-body .content-container {
    min-width: 300px;
  }
}
.opt-body .columns {
  display: inline-block;
  float: none;
  width: 100%;
}
.opt-body .main-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 55px;
  padding: 15px 24px;
  width: auto;
  height: 55px;
  border-radius: 3px;
  box-shadow: 3px 5px 9px 0 rgba(0, 0, 0, 0.3);
  text-decoration: none;
  text-shadow: none;
  border: none;
  font-family: "CenturyGothic-Bold";
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  background: #ffc600;
  color: #1d2126;
  cursor: pointer;
  transition: 0.4s all;
}
.opt-body .main-btn:hover {
  background: #e2bc85;
  color: #ffffff;
}
.opt-body .header {
