}
.opt-body .logo p {
  font-family: "IBM Plex Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #323232;
  padding: 4px 0;
  margin-bottom: 0;
  margin-left: 25px;
  margin-top: 0;
}
@media (max-width: 767px) {
